import React, { useState, useEffect, useRef } from 'react';
import { ReactComponent as VectorOne } from './shapes/Vector-1.svg';
import { ReactComponent as VectorTwo } from './shapes/Vector-2.svg';
import { ReactComponent as Mobile } from './shapes/mobile.svg';
import { ReactComponent as Paint } from './shapes/paint-brush.svg';
import { ReactComponent as Laptop } from './shapes/laptop.svg';
import { ReactComponent as Microphone } from './shapes/microphone.svg';
import { ReactComponent as VectorHeader } from './shapes/Vector-header.svg';
import BooksAndBrews from './images/books-and-brewskies.jpeg';
import HeaderImage from './images/header.png';
import LetsLink from './images/letslink.png';
import Habts from './images/Habts.png';
import './App.scss';

const App = () => {
    const [name, setName] = useState();
    const [email, setEmail] = useState();
    const [message, setMessage] = useState();
    const [loading, setLoading] = useState(false);
    const [showBack, setShowBack] = useState(false);
    const [scrollPosition, setScrollPosition] = useState();
    const contactRef = useRef();
    const projectsRef = useRef();
    const expertiseRef = useRef();
    const resume = useRef();


    useEffect(() => {
        //scroll listener
        window.addEventListener('scroll', () => {
            let pos = document.body.scrollTop || document.documentElement.scrollTop
            setScrollPosition(pos)
        })
    }, [])

    const onMenuItemClick = (name) => {
        switch (name) {
            case 'expertise':
                return window.scrollTo({
                    top: expertiseRef.current.offsetTop,
                    behavior: 'smooth'
                });
            case 'projects':
                return window.scrollTo({
                    top: projectsRef.current.offsetTop - 50,
                    behavior: 'smooth'
                });
            case 'contact':
                return window.scrollTo({
                    top: contactRef.current.offsetTop,
                    behavior: 'smooth'
                });
            case 'resume':
                return window.scrollTo({
                    top: resume.current.offsetTop - 50,
                    behavior: 'smooth'
                });
            default:
                window.scrollTo({
                    top: 0,
                    behavior: 'smooth'
                });
        }
    }


    const sendEmail = () => {
        if (!name || !message || !email) return alert('All fields required')
        setLoading(true)

        fetch("https://formsubmit.co/ajax/6fb9b6db755f974fe6a7d77b04c47d75", {
            method: "POST",
            headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
            },
            body: JSON.stringify({
                name: name,
                message: message,
                email: email
            })
        })
            .then(response => response.json())
            .then(data => {
                console.log(data)
                setLoading(false)
                alert("Thank you for reaching out. I will respond as soon as I can.")
            })
            .catch(error => {
                console.log(error)
                alert('Sorry, something went wrong trying to send your request. Please try again.')
                setLoading(false)
            });
    }

    return (
        <div className="container">
            <VectorHeader className='vector-header' />
            <nav className={`navbar flex ${scrollPosition > 10 ? 'background-white' : 'background-transparent'}`}>
                <div className="col-1">
                    <p className="logo-name" onClick={() => onMenuItemClick('')}>Portfolio
                    </p>
                </div>
                <div className="col-9 nav-divider" />
                <div className="col flex">
                    <div className="menu col flex justify-content-space-evenly">
                        <p className="menu-item m-left-4" onClick={() => onMenuItemClick('expertise')}>my expertise</p>
                        <p className="menu-item m-left-4" onClick={() => onMenuItemClick('projects')}>my projects</p>
                        <p className="menu-item m-left-4" onClick={() => onMenuItemClick('resume')}>resume</p>
                        <p className="menu-item m-left-4" onClick={() => onMenuItemClick('contact')}>contact</p>
                    </div>
                </div>
            </nav>

            <section className="header flex">
                <div className="col flex justify-content-center">
                    <img src={HeaderImage} alt='my header svg' className='align-self-center' />
                </div>
                <div className="col flex direction-column justify-content-center align-items-center text-align-center">
                    <div className="header-text p-2">
                        <h2>Hi, I'm</h2>
                        <h1><div className='flat-circle' />JOHN NGUYEN</h1>
                        <h2>And I love to CREATE THINGS.</h2>
                    </div>
                </div>
            </section>

            <section className="expertise flex direction-column" ref={expertiseRef}>
                <div className="row">
                    <div className="col text-align-center">
                        <h1 className='text-align-center section-title'>My Expertise</h1>
                    </div>
                </div>
                <div className="row">
                    <div className="col flex justify-content-center">
                        <div className="expertise-item">
                            <Mobile />
                            <p>Everyone has a smart phone now-a-days, so I taught myself how to create mobile apps with React Native.</p>
                        </div>
                    </div>
                    <div className="col flex justify-content-center">
                        <div className="expertise-item">
                            <Laptop />
                            <p>I don’t program just to program, but I do it to create a masterpiece. React is my speciality.</p>
                        </div>
                    </div>
                </div>
                <div className="row">
                    <div className="col flex justify-content-center">
                        <div className="expertise-item">
                            <Microphone className='custom-svg' />
                            <p>I value my relationships with my clients and colleagues, and I maintain healthy and honest relationships with them.</p>
                        </div>
                    </div>
                    <div className="col flex justify-content-center">
                        <div className="expertise-item">
                            <Paint className='custom-svg' />
                            <p>I don’t consider myself artistic, but I know a beautiful piece when I see one. I design and create all of my projects.</p>
                        </div>
                    </div>
                </div>
            </section>

            <section className="projects" ref={projectsRef}>
                <div>
                    <div className="row">
                        <div className="col text-align-center">
                            <h1 className='text-align-center section-title'>My Projects</h1>
                        </div>
                    </div>

                    <div className="row">
                        <div className="col flex align-items-center">
                            <p><b>Books and Brewskies</b>, which was a ecommerce website, was my first exposure to web applications.</p>
                        </div>
                        <div className="col-7 flex align-items-center">
                            <div className="img-books position-relative">
                                <a href='https://booksandbrewskies.herokuapp.com/'><img src={BooksAndBrews} alt='booksandbrewskies' width='100%' />
                                    <div className="img-overlay" />
                                    <ul className='list list-books'>
                                        <li>ReactJs</li>
                                        <li>MongoDb</li>
                                        <li>NodeJs</li>
                                        <li>Scss</li>
                                    </ul>
                                </a>
                            </div>
                        </div>
                    </div>

                    <div className="row position-relative">
                        <div className="col text-align-center p-1">
                            <p>After creating <b>Books and Brewskies</b>, I had an idea that allowed users to meet with people within a short radius. The idea was to help people meet other people and still social distance (Covid).</p>
                            <VectorOne className='projects-vector projects-vector-2' />
                        </div>
                    </div>

                    <div className="row">
                        <div className="col flex align-items-center">
                            <p><b>Letslink</b> was built with React Native. Main functionalities include; map and list view to show people near you, send request, profile page, and messaging.</p>
                        </div>
                        <div className="col-5">
                            <div className="img-link position-relative">
                                <img src={LetsLink} alt='Letslink' width='100%' />
                                <div className="img-overlay" />
                                <ul className='list list-link'>
                                    <li>React Native</li>
                                    <li>Firebase</li>
                                </ul>
                            </div>
                        </div>
                    </div>

                    <div className="row position-relative">
                        <div className="col text-align-center p-1">
                            <p><b>Letslink</b> was a big idea and required a couple months to get the foundation of the app built. After I finished the foundation of LetsLink, I read a book called “Atomic Habits” and sparked another project.</p>
                            <VectorTwo className='projects-vector projects-vector-3' />
                        </div>
                    </div>


                    <div className="row">
                        <div className="col-5">
                            <div className="img-habts position-relative">
                                <img src={Habts} alt='Habts' width='100%' />
                                <div className="img-overlay" />
                                <ul className='list list-habts'>
                                    <li>React Native</li>
                                    <li>Firebase</li>
                                </ul>
                            </div>
                        </div>
                        <div className="col flex align-items-center">
                            <p><b>Habts</b> is also built with React Native. Main Functionlities include; create, update, track habits, notifications/reminders, reward system (badges), and reflection tool.</p>
                        </div>
                    </div>

                    <div className="row">
                        <div className="col text-align-center p-1 position-relative">
                            <h2>Projects built previously:</h2>
                            <ul className='position-relative'>
                                <li>Student Check-In App (Andriod) for the University of Montana</li>
                                <li>Dating App (Visual Basic App for school project)</li>
                                <VectorTwo className='projects-vector projects-vector-1' />
                            </ul>
                        </div>
                    </div>


                    <div className="row">
                        <div className="col text-align-center">
                            <p className='underline position-relative'>
                                More Projects To Come...
                            </p>
                        </div>
                    </div>

                </div>
            </section>

            <section className="resume" ref={resume}>
                <div className="row">
                    <div className="col text-align-center">
                        <h1 className='text-align-center section-title'>My Resume</h1>
                    </div>
                </div>

                <div className="row">
                    <div className="col flex justify-content-center position-relative" onClick={() => setShowBack(showBack ? false : true)}>
                        <div className={`cv cv-front ${showBack ? 'cv-hide' : 'cv-show'}`} />
                        <div className={`cv cv-back ${showBack ? 'cv-show' : 'cv-hide'}`} />
                    </div>
                </div>
            </section>

            <section className="contact" ref={contactRef}>
                <div className="row justify-content-center">
                    <div className="col-5">
                        <div className='form'>
                            <h1 className='text-align-center section-title'>Contact Me</h1>

                            <label>
                                Name:
                                <input type='text' onChange={(e) => setName(e.target.value)} />
                            </label>
                            <label>
                                Email:
                                <input type='text' onChange={(e) => setEmail(e.target.value)} />
                            </label>
                            <label>
                                Message:
                                <textarea type='text' onChange={(e) => setMessage(e.target.value)} />
                            </label>
                            <button onClick={sendEmail}>{loading ? 'Sending..' : 'Send'}</button>
                        </div>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default App;

